	module.exports = {"data":{"markdownRemark":{"html":"<h2>Ruby has changed over time</h2>\n<p>Ruby was been in constant development since the 1990s, and like many languages,\nthere have been syntax changes across versions, so it is important to be clear\nabout which Ruby version your code expects.</p>\n<p>Probably the most visible change came with Ruby 1.9; previously, we wrote\nhashes like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>  <span class=\"token punctuation\">{</span> <span class=\"token symbol\">:one</span> <span class=\"token operator\">=</span><span class=\"token operator\">></span> <span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:two</span> <span class=\"token operator\">=</span><span class=\"token operator\">></span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:three</span> <span class=\"token operator\">=</span><span class=\"token operator\">></span> <span class=\"token number\">3</span> <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>This use of the ‘hashrocket’ operator (<code>=></code>) was so common, that Ruby 1.9\nprovided a shorthand:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>  { one: 1, two: 2, three: 3 }</code></pre>\n      </div>\n<p>This older code run on any version, but the newer syntax will only run on Ruby 1.9+.</p>\n<h2>How does this cause problems?</h2>\n<p>For example, you might have decided to use a Gem which internally relies on\nRuby 1.9 features; this means that your project now also relies on Ruby 1.9\nfeatures.</p>\n<p>If you don’t specify which version of Ruby your project needs, it can be very\nconfusing when code works on one machine, but not another.</p>\n<p>As with most languages, it’s considered good practice to specify the version of\nRuby that your code expects. This makes it much easier to manage multiple\nprojects on your development machine, each expecting a different version of\nRuby.</p>\n<h2>How do I specify my Ruby version?</h2>\n<p>There are a couple of tools which are popular for this, but both have agreed to\nshare a common file. Many Ruby (or Rails) projects will include a simple\n<code>.ruby-version</code> file, which simply specifies a version number, <em>eg</em>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>2.4.2</code></pre>\n      </div>\n<p>Popular tools to help you manage your Ruby version are:</p>\n<ul>\n<li><a href=\"https://rvm.io\">Ruby Version Manager (RVM)</a></li>\n<li><a href=\"https://github.com/rbenv/rbenv\">rbenv</a></li>\n</ul>\n<p>Let’s look at RVM.</p>\n<h3>Using RVM</h3>\n<p>RVM is typically installed (<a href=\"https://rvm.io\">link</a>) on a Linux, Unix or MacOS\nmachine, and is very convenient as it hooks into the <code>cd</code> (<code>c</code>hange <code>d</code>irectory)\ncommand so when you move to a new project, your <code>.ruby-version</code> is read\nautomatically, and you’re automatically switched to the correct version of Ruby\nbefore you start working.</p>\n<p>For example, you might have this sequence:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>% cd ~/projects/older-project\n% ruby --version\n\nruby 2.3.5p376 (2017-09-14 revision 59905) [x86_64-darwin16]\n\n% cd ~/projects/newer-project\n% ruby --version\n\nruby 2.4.2p198 (2017-09-14 revision 59899) [x86_64-darwin16]</code></pre>\n      </div>\n<p>(These examples are from a MacOS machine)</p>","fields":{"slug":"/ruby/managing-ruby-versions/"},"frontmatter":{"title":"Managing Ruby versions"}}},"pathContext":{"slug":"/ruby/managing-ruby-versions/"}}

